.bottomPart {
  width: 100vw;
  min-height: 50vh;
  background: white;
  position: absolute;
  top: 50vh;

  .bottomTitle {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    column-gap: 5vw;
    padding: 2vh 5vw 0;

    .decoration {
      flex: 1;
      height: 0.5vh;
      background-color: #015E4B;
      border-radius: 1vh;
    }

    .about {
      color: #DE9783;
      text-align: center;
      font: {
        weight: 500;
        size: 3vw;
        family: Roboto;
      }
    }
  }

  .information {
    margin-top: 5vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    .learnAbout {
      height: fit-content;
      display: flex;
      flex-direction: column;
      align-items: center;
      row-gap: 3vh;

      .text {
        margin-left: 5vw;
        display: flex;
        flex-direction: row;
        width: 40vw;

        .aligner {
          background-color: #015E4B;
          width: 5px;
          margin-right: 1vw;
          border-radius: 5px;
        }

        .ps {
          color: #686868;
          font: {
            weight: 400;
            size: 1vw;
          }
        }
      }

      button {
        background-color: #015E4B;
        width: 25vw;
        border-radius: 0.7vw;
        border: none;
        padding: 3vh 0;
        color: white;
        text-decoration: none;
        font: {
          weight: 400;
          size: 1vw;
        }
        cursor: pointer;
        transition-duration: 0.2s;

        &:hover {
          background-color: #026d57;
        }
      }
    }

    > div:not(.learnAbout) {
      display: flex;
      flex-direction: row;
      align-items: center;

      > img {
        margin-right: 5vw;
      }

      .socials {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        width: 5vw;
        margin-right: 5vw;
      }
    }
  }
}