.social {
  width: 5vw;
  height: 5vw;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;

  svg {
    height: 2.5vw;
    margin: 0;
    color: #DE9783;
    fill: #DE9783;
    cursor: pointer;
    transition-duration: 0.2s;


  }

  &:hover > svg {
    filter: drop-shadow(0px 0px 5px rgb(0 0 0 / 0.7));
    height: 2.3vw;

  }

}